import { Injectable } from '@angular/core';
import {
  CanActivate,
  CanDeactivate,
  CanLoad,
  ActivatedRouteSnapshot,
  RouterStateSnapshot,
  Route,
  UrlSegment
} from '@angular/router';
import { Observable } from 'rxjs';
import { SendInformationService } from '../service/SendInformation/send-information.service';
import { STEP_ID } from '../CONST';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class SecurityAccessGuard implements CanDeactivate<any>, CanActivate, CanLoad {

  guards: boolean;
  constructor(
    private guard: SendInformationService<any>
  ) {
    this.guards = environment.guards;
  }

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<boolean> | boolean {
    const stepId = route.data.stepId;
    if ( !this.guards ) {
      return true;
    }
    return this.guard.lastValue(STEP_ID) === stepId;
  }

  canDeactivate(
    component: any,
    currentRoute: ActivatedRouteSnapshot,
    currentState: RouterStateSnapshot,
    nextState: RouterStateSnapshot
  ): Observable<boolean> | boolean {
    const stepId = currentRoute.data.stepId;
    if ( !this.guards ) {
      return true;
    }
    return this.guard.lastValue(STEP_ID) !== stepId;
  }

  canLoad(route: Route, segments: UrlSegment[]): boolean {
    const stepsId: Array<string> = route.data.child;
    if ( !this.guards ) {
      return true;
    }
    return stepsId.map( stepId => this.guard.lastValue(STEP_ID) === stepId)
    .reduce((previousValue, currentValue) => previousValue || currentValue, false);
  }
}
